const Header = ({ siteTitle }) => {
  const [isOpen, toggle] = useState(false);
  // console.log(isOpen);
  return (
    <header
      style={{
        background: `rebeccapurple`,
        marginBottom: `1.45rem`,
      }}
    >
      <div
        style={{
          margin: `0 auto`,
          maxWidth: 960,
          padding: `1.45rem 1.0875rem`,
          display: `flex`,
          justifyContent: `space-between`,
          alignItems: "center",
        }}
      >
        <h1 style={{ margin: 0 }}>
          <Link
            to="/"
            style={{
              color: `white`,
              textDecoration: `none`,
            }}
          >
            {siteTitle}
          </Link>
        </h1>
        {/* <nav>Home|About Me|Projects|Experience|Resume</nav> */}
        <div className="div-links">
          {" "}
          <Link
            to="/"
            style={{
              marginLeft: 0,
            }}
            className="link"
            activeStyle={{ color: "red" }}
          >
            Home
          </Link>
          <Link to="/about-me" className="link" activeStyle={{ color: "red" }}>
            About Me
          </Link>
          <Link to="/projects" className="link" activeStyle={{ color: "red" }}>
            Projects
          </Link>
          <Link
            to="/experience"
            className="link"
            activeStyle={{ color: "red" }}
          >
            Experience
          </Link>
          <a href="Ariel.Ahdoot.pdf" target="_blank" className="link">
            Resume
          </a>
        </div>
        <div className="dropdown">
          <button className="dropbtn" onClick={() => toggle(!isOpen)}>
            <div className="navbar-button" />
            <div className="navbar-button" />
            <div className="navbar-button" />
          </button>
          {isOpen && (
            <div className="dropdown-content" id="dropdown">
              <Link to="/">Home</Link>
              <Link to="/about-me">About Me</Link>
              <Link to="/projects">Projects</Link>
              <Link to="/experience">Experience</Link>
              <a href="Ariel.Ahdoot.pdf" target="_blank">
                Resume
              </a>
            </div>
          )}
        </div>{" "}
      </div>
    </header>
  );
};

Header.propTypes = {
  siteTitle: PropTypes.string,
};

Header.defaultProps = {
  siteTitle: ``,
};

export default Header;
